.dozvonBody{
    background-color: #100e1f;
    min-height: 100vh;
    padding: 15px;
    margin: 0px;
    font-family: 'Josefin Sans', sans-serif;
}

.dozvonMainBlock{
    margin-top: 20px;
    background-color: #252434;
    height: max-content;
    width: max-content;
    padding: 20px 20px;
    border-radius: 6px;
}

.dozvonMainHeader{
    background-color: #3a374a;
    width: max-content;
    height: 40px;
    border-radius: 6px;
    display: flex;
    color: white;
    font-family: 'Montserrat', sans-serif;
    font-weight:500;
    font-size: 12px;
    display: flex;
    align-items: center;
    padding-left: 20px;
}

.RegBlockDozvon{
    width: 133px;
    padding-right: 12px;
}
.GeoBlockDozvon{
    width: 68px;
    padding-right: 12px;
}
.StatusBlockDozvon{
    width: 123px;
    padding-right: 12px;
}
.TestBlockDozvon{
